const GeoJson = 
{
    processor( payload )
    {
        return new Promise( resolve =>
            {
                request( payload.url ).then( gjson =>
                    {
                        var result = Leaflet.geoJSON( gjson ).bindPopup( layer =>
                        {
                            return layer.feature.properties.description;
                        })
                        resolve( result )
                    })
            })
    }
}


